import React from 'react'
import Marquee from "react-fast-marquee";
import {motion} from "framer-motion"

export default function RunningText() {
  return (
    <motion.nav
    initial={{ opacity: 0 }}
            animate={{ opacity: 1}}
            transition={{
              ease: "linear",
              duration: 10,
              delay:8,
            }}
    > 
    <Marquee pauseOnHover>
     <ul className="companySkills_items">
        <li>Цивільний процес</li>
       <li>Виконавче впровадження</li>
       <li>Кримінальний процес</li>
       <li>Сімейне право</li>
       <li>Інтелектуальна власність</li>
       <li>Адміністративний процес</li>
       <li>Реєстрація та супровід бізнесу в ЄС</li>
     </ul>
      </Marquee>
   </motion.nav>
  )
}

